html {
  font-size: 625%;
}
body,
html {
  width: 100%;
  height: 100%;
}
body {
  font-size: .16rem;
  color: #666;
  background: #f0f0f7;
}
html,
body,
div,
table,
ul,
li,
ol,
dl,
dd,
dt,
a,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
em,
i {
  font-style: normal;
}
strong {
  font-weight: normal;
}
img {
  max-width: 100%;
  display: block;
}
textarea {
  resize: none;
}
form {
  margin: 0;
}
button {
  outline: none!important;
}
button:focus {
  outline: none!important;
}
a {
  color: #4f5f6f;
}
a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  color: #2286d3;
}
.wrap {
  width: 100%;
  margin: 0 auto;
}
.theme-color {
  color: #77a726;
}
.warn-text {
  color: #fe8358;
}
.btn-primary {
  background: #77a726;
  color: #fff;
  font-weight: normal;
  text-shadow: none;
  border-color: #77a726;
}
.btn-primary:hover,
.btn-primary:focus {
  background: #77a726;
  color: #fff;
  font-weight: normal;
  text-shadow: none;
  border-color: #77a726;
}
.btn-primary:active:hover,
.btn-primary.active:hover,
.open > .dropdown-toggle.btn-primary:hover,
.btn-primary:active:focus,
.btn-primary.active:focus,
.open > .dropdown-toggle.btn-primary:focus,
.btn-primary:active.focus,
.btn-primary.active.focus,
.open > .dropdown-toggle.btn-primary.focus {
  background: #77a726;
  color: #fff;
  font-weight: normal;
  text-shadow: none;
  border-color: #77a726;
}
.btn-default {
  background: #ffffff;
  border-color: #dee7ef;
  font-weight: normal;
  text-shadow: none;
  color: #4e5666;
}
.btn-default:hover,
.btn-default:focus {
  background: #ffffff;
  color: #4e5666;
  font-weight: normal;
  text-shadow: none;
  border-color: #dee7ef;
}
.btn-default:active:hover,
.btn-default.active:hover,
.open > .dropdown-toggle.btn-default:hover,
.btn-default:active:focus,
.btn-default.active:focus,
.open > .dropdown-toggle.btn-default:focus,
.btn-default:active.focus,
.btn-default.active.focus,
.open > .dropdown-toggle.btn-default.focus {
